.left-panel {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 1.5rem 0;
	background: linear-gradient(180deg, #eaf3ff 0%, #ffffff 100%);
	border-radius: 16px;
}


/* Illustration container */
.lp-illus { width: 100%; max-width: 560px; }
.lp-illus__svg { width: 100%; height: auto; display: block; }
.lp-float { animation: circleFloat 6s ease-in-out infinite; }
.lp-crane { animation: circleFloat 7s ease-in-out infinite; }
.lp-y { filter: drop-shadow(0 8px 16px rgba(0,0,0,0.06)); }

/* Image-based hero with glow and floating icons */
.lp-image { position: relative; width: clamp(260px, 40vw, 520px); aspect-ratio: 4/3; border-radius: 16px; overflow: hidden; box-shadow: 0 24px 48px rgba(14, 165, 233, 0.22); }
.lp-img { width: 100%; height: 100%; object-fit: cover; filter: brightness(1.08) contrast(1.03) saturate(1.05); transform: scale(1.02); }
.lp-image::after { content: ""; position: absolute; inset: 0; background: radial-gradient(ellipse at 20% 10%, rgba(233,30,99,0.12), transparent 50%), radial-gradient(ellipse at 80% 90%, rgba(46,125,50,0.12), transparent 50%); pointer-events: none; }

.lp-icon { position: absolute; width: 22px; height: 22px; border-radius: 50%; background: white; box-shadow: 0 8px 18px rgba(2,6,23,0.18); }
.lp-icon::after { content: ""; position: absolute; inset: 4px; border-radius: 50%; background: conic-gradient(from 0deg, #E91E63, #2E7D32); }
.lp-icon--1 { left: 14%; top: 18%; animation: float1 6s ease-in-out infinite; }
.lp-icon--2 { right: 12%; top: 34%; animation: float2 7s ease-in-out infinite; }
.lp-icon--3 { left: 38%; bottom: 12%; animation: float3 5.5s ease-in-out infinite; }

@keyframes float1 { 0%,100% { transform: translateY(0) } 50% { transform: translateY(-10px) } }
@keyframes float2 { 0%,100% { transform: translate(0,0) } 50% { transform: translate(-8px, -6px) } }
@keyframes float3 { 0%,100% { transform: translate(0,0) } 50% { transform: translate(6px, -8px) } }

/* Rotating glow ring */
.left-panel__circle::after {
	content: '';
	position: absolute;
	inset: -6px;
	border-radius: 50%;
	background: conic-gradient(from 0deg, rgba(46,125,50,0.15), rgba(255,77,121,0.15), rgba(46,125,50,0.15));
	filter: blur(10px);
	animation: ringSpin 10s linear infinite;
	pointer-events: none;
}

@keyframes ringSpin {
	0% { transform: rotate(0deg); }
	100% { transform: rotate(360deg); }
}

/* Orbiting dots */
.left-panel__orbit {
	position: absolute;
	width: 6px;
	height: 6px;
	border-radius: 50%;
	background: #2E7D32;
	box-shadow: 0 0 8px rgba(46,125,50,0.4);
}

.left-panel__orbit--1 {
	left: 50%;
	top: 50%;
	transform-origin: -80px -80px;
	animation: orbit1 7s linear infinite;
}

.left-panel__orbit--2 {
	left: 50%;
	top: 50%;
	transform-origin: 90px 90px;
	background: #ff4d79;
	box-shadow: 0 0 8px rgba(255,77,121,0.4);
	animation: orbit2 9s linear infinite;
}

@keyframes orbit1 {
	0% { transform: rotate(0deg) translate(-80px, -80px); }
	100% { transform: rotate(360deg) translate(-80px, -80px); }
}

@keyframes orbit2 {
	0% { transform: rotate(0deg) translate(90px, 90px); }
	100% { transform: rotate(360deg) translate(90px, 90px); }
}

.left-panel__y {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-family: 'Poppins', 'Inter', system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
	font-weight: 800;
	font-size: clamp(56px, 10vw, 96px);
	letter-spacing: 2px;
	color: #0ea5a3;
	opacity: 0.12;
	-webkit-user-select: none;
	user-select: none;
}

/* Routes */
.left-panel__route {
	position: absolute;
	background: linear-gradient(90deg, rgba(16, 185, 129, 0.85) 0%, rgba(5, 150, 105, 0.85) 100%);
	box-shadow: 0 2px 8px rgba(16, 185, 129, 0.25);
	border-radius: 999px;
}

.left-panel__route--h {
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 70%;
	height: 4px;
}

.left-panel__route--v {
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	height: 70%;
	width: 4px;
}

/* Markers */
.left-panel__marker {
	position: absolute;
	width: 14px;
	height: 14px;
	border-radius: 50%;
	background: #10b981;
	box-shadow: 0 0 0 0 rgba(16, 185, 129, 0.4);
	animation: markerPulse 2.2s ease-in-out infinite;
	outline: 2px solid #ffffff;
}

.left-panel__marker::after {
	content: '';
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 6px;
	height: 6px;
	border-radius: 50%;
	background: #ffffff;
}

.left-panel__marker--left {
	top: 50%;
	left: calc(50% - var(--route));
	transform: translate(-50%, -50%);
}

.left-panel__marker--right {
	top: 50%;
	left: calc(50% + var(--route));
	transform: translate(-50%, -50%);
}

.left-panel__marker--top {
	top: calc(50% - var(--route));
	left: 50%;
	transform: translate(-50%, -50%);
}

.left-panel__marker--bottom {
	top: calc(50% + var(--route));
	left: 50%;
	transform: translate(-50%, -50%);
}

/* Buses */
.left-panel__bus {
	position: absolute;
	filter: drop-shadow(0 6px 12px rgba(0, 0, 0, 0.1));
}

.left-panel__bus--h {
	top: 50%;
	left: calc(50% - var(--route));
	transform: translate(-50%, -50%);
	animation: moveH 7s ease-in-out infinite;
}

.left-panel__bus--v {
	top: calc(50% - var(--route));
	left: 50%;
	transform: translate(-50%, -50%);
	animation: moveV 7s ease-in-out infinite;
}

/* Animations */
@keyframes moveH {
	0% { left: calc(50% - var(--route)); }
	50% { left: calc(50% + var(--route)); }
	100% { left: calc(50% - var(--route)); }
}

@keyframes moveV {
	0% { top: calc(50% - var(--route)); }
	50% { top: calc(50% + var(--route)); }
	100% { top: calc(50% - var(--route)); }
}

@keyframes markerPulse {
	0% { box-shadow: 0 0 0 0 rgba(16, 185, 129, 0.4); }
	70% { box-shadow: 0 0 0 10px rgba(16, 185, 129, 0); }
	100% { box-shadow: 0 0 0 0 rgba(16, 185, 129, 0); }
}

@keyframes circleFloat {
	0%, 100% { transform: translateY(0); }
	50% { transform: translateY(-6px); }
}

/* Reduced motion */
@media (prefers-reduced-motion: reduce) {
	.left-panel__circle { animation: none !important; }
	.left-panel__bus--h { animation: none !important; }
	.left-panel__bus--v { animation: none !important; }
	.left-panel__marker { animation: none !important; }
}

/* Responsive tweaks */
@media (max-width: 1024px) {
	.left-panel { padding: 1.25rem 0; }
}

@media (max-width: 640px) {
	.left-panel { padding: 1rem 0; border-radius: 12px; }
}

/* Left Panel Container */
.left-panel {
	width: 100%;
	background: linear-gradient(160deg, #eef6ff 0%, #ffffff 100%);
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 1.5rem 1rem;
}

/* Circular visual container */
.left-panel__circle {
	position: relative;
	width: clamp(260px, 40vw, 520px);
	height: clamp(260px, 40vw, 520px);
	border-radius: 50%;
	background: radial-gradient(120% 120% at 50% 40%, #dcfce7 0%, #bbf7d0 50%, #a7f3d0 100%);
	box-shadow: 0 20px 45px rgba(16, 185, 129, 0.25), inset 0 1px 2px rgba(255,255,255,0.5);
	overflow: hidden;
	animation: circleFloat 7s ease-in-out infinite;
}

/* Center Y */
.left-panel__y {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-weight: 800;
	font-size: clamp(64px, 10vw, 120px);
	line-height: 1;
	color: rgba(4, 120, 87, 0.15);
	text-shadow: 0 2px 6px rgba(0,0,0,0.05);
}

/* Routes */
.left-panel__route {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	background: linear-gradient(90deg, rgba(16,185,129,0.9), rgba(5,150,105,0.9));
	filter: drop-shadow(0 2px 6px rgba(16,185,129,0.25));
	border-radius: 999px;
}

.left-panel__route--h {
	width: 80%;
	height: 4px;
	background-size: 16px 4px;
	background-image: linear-gradient(to right, rgba(16,185,129,1) 8px, rgba(0,0,0,0) 8px);
	animation: dashMoveX 6s linear infinite;
}

.left-panel__route--v {
	height: 80%;
	width: 4px;
	background-size: 4px 16px;
	background-image: linear-gradient(to bottom, rgba(16,185,129,1) 8px, rgba(0,0,0,0) 8px);
	animation: dashMoveY 6s linear infinite;
}

/* Markers (map pins) */
.left-panel__marker {
	position: absolute;
	width: 14px;
	height: 14px;
	border-radius: 50%;
	background: #10b981;
	box-shadow: 0 4px 10px rgba(16, 185, 129, 0.35);
	animation: markerPulse 2.2s ease-in-out infinite;
}

.left-panel__marker::after {
	content: '';
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	width: 6px;
	height: 6px;
	border-radius: 50%;
	background: #ffffff;
}

.left-panel__marker--left { left: 10%; top: 50%; transform: translate(-50%, -50%); }
.left-panel__marker--right { left: 90%; top: 50%; transform: translate(-50%, -50%); }
.left-panel__marker--top { left: 50%; top: 10%; transform: translate(-50%, -50%); }
.left-panel__marker--bottom { left: 50%; top: 90%; transform: translate(-50%, -50%); }

/* Bus icons moving along routes */
.left-panel__bus {
	position: absolute;
	filter: drop-shadow(0 8px 16px rgba(0,0,0,0.15));
}

.left-panel__bus--h {
	left: 10%;
	top: 50%;
	transform: translate(-50%, -50%);
	animation: busMoveX 7s ease-in-out infinite;
}

.left-panel__bus--v {
	left: 50%;
	top: 10%;
	transform: translate(-50%, -50%);
	animation: busMoveY 7s ease-in-out infinite;
}

/* Keyframes */
@keyframes dashMoveX {
	0% { background-position-x: 0; }
	100% { background-position-x: 16px; }
}

@keyframes dashMoveY {
	0% { background-position-y: 0; }
	100% { background-position-y: 16px; }
}

@keyframes markerPulse {
	0%, 100% { transform: translate(-50%, -50%) scale(1); opacity: 0.9; }
	50% { transform: translate(-50%, -50%) scale(1.15); opacity: 1; }
}

@keyframes busMoveX {
	0% { left: 10%; }
	50% { left: 90%; }
	100% { left: 10%; }
}

@keyframes busMoveY {
	0% { top: 10%; }
	50% { top: 90%; }
	100% { top: 10%; }
}

@keyframes circleFloat {
	0%, 100% { transform: translateY(0); }
	50% { transform: translateY(-6px); }
}

/* Responsiveness */
@media (max-width: 1024px) {
	.left-panel { padding: 1.25rem 0.75rem; }
}

@media (max-width: 640px) {
	.left-panel {
		padding: 1rem 0.5rem;
	}
	.left-panel__circle {
		width: clamp(240px, 78vw, 360px);
		height: clamp(240px, 78vw, 360px);
	}
}


/* === Enhancements for brand-compliant design and animations === */
/* Softer teal  light blue circle with glowing ring */
.left-panel__circle {
	background: radial-gradient(120% 120% at 50% 40%, #d7f3ff 0%, #e6fffb 60%, #ffffff 100%);
	box-shadow: 0 24px 48px rgba(30, 136, 229, 0.15), 0 2px 8px rgba(13, 148, 136, 0.12);
}

.left-panel__circle::before {
	content: '';
	position: absolute;
	inset: -10px;
	border-radius: 50%;
	background: radial-gradient(closest-side, rgba(46, 125, 50, 0.25), transparent 70%);
	filter: blur(10px);
	z-index: 0;
}

/* Center Y with gradient, fade-in and periodic glow */
.left-panel__y {
	font-weight: 900;
	font-size: clamp(100px, 12vw, 140px);
	letter-spacing: 1px;
	background: linear-gradient(135deg, #ff4d79 0%, #2E7D32 100%);
	-webkit-background-clip: text;
	background-clip: text;
	color: transparent;
	opacity: 0;
	animation: yFadeIn 0.6s ease-out forwards, yGlow 3s ease-in-out infinite 0.6s;
}

@keyframes yFadeIn {
	from { opacity: 0; transform: translate(-50%, -54%); }
	to { opacity: 1; transform: translate(-50%, -50%); }
}

@keyframes yGlow {
	0%, 100% { filter: drop-shadow(0 0 0px rgba(255, 77, 121, 0)) drop-shadow(0 0 0px rgba(46, 125, 50, 0)); }
	50% { filter: drop-shadow(0 8px 16px rgba(255, 77, 121, 0.25)) drop-shadow(0 12px 22px rgba(46, 125, 50, 0.25)); }
}

/* Dashed, glowing routes in #2E7D32 */
.left-panel__route {
	background: transparent;
}

.left-panel__route--h {
	width: 70%;
	height: 0;
	border-top: 3px dashed #2E7D32;
	box-shadow: 0 0 10px rgba(46, 125, 50, 0.25);
}

.left-panel__route--v {
	height: 70%;
	width: 0;
	border-left: 3px dashed #2E7D32;
	box-shadow: 0 0 10px rgba(46, 125, 50, 0.25);
}

/* Modern pulsing map markers */
.left-panel__marker {
	width: 16px;
	height: 16px;
	background: #2E7D32;
	box-shadow: 0 2px 10px rgba(46, 125, 50, 0.35);
	animation: markerPulse 2.2s ease-in-out infinite;
}

.left-panel__marker::after {
	width: 6px;
	height: 6px;
}

/* === Compact overrides to reduce visual zoom === */
.left-panel {
	padding: 0.5rem 0.25rem;
}

.left-panel__circle {
	width: clamp(200px, 28vw, 300px);
	height: clamp(200px, 28vw, 300px);
	--route: 25%;
}

.left-panel__y {
	font-size: clamp(72px, 7.5vw, 96px);
}

.left-panel__route--h {
	width: 58%;
	border-top-width: 2px;
}

.left-panel__route--v {
	height: 58%;
	border-left-width: 2px;
}

.left-panel__marker {
	width: 10px;
	height: 10px;
}

.left-panel__marker::after {
	width: 3px;
	height: 3px;
}

.left-panel__bus--h {
	transform: translate(-50%, -50%) scale(0.7);
}

.left-panel__bus--v {
	transform: translate(-50%, -50%) scale(0.7);
}

